// src/i18n.js
import Vue from 'vue'
import VueI18n from 'vue-i18n'
import en from './locales/en'
import zh from './locales/zh'


Vue.use(VueI18n)

// 
const messages = {
    en,
    zh,
}

//  VueI18n 
const i18n = new VueI18n({
    locale: 'zh', // 
    messages, // 
})

export default i18n
