<template>
  <div class="card-container my-2 w-100 h-100 bg-white overflow-hidden rounded-4 " :class="isShadow? 'shadow-lg': ''" @mouseenter="showOverlay = true" @mouseleave="showOverlay = false">
    <slot></slot> <!--  -->
    <router-link :to="toURL" class="text-decoration-none ">
      <div v-show="showOverlay" class="overlay rounded-4 border-0 h-100 overflow-auto  wow fadeIn" data-wow-duration="0.5s">
        <slot name="overlay"></slot> <!--  -->
      </div>
    </router-link>
    
  </div>
</template>

<script>


export default {
  props:{
    toURL: {
      type: String,
      required: true
    },
    isShadow: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  data() {
    return {
      showOverlay: false,
    };
  },
};
</script>

<style lang="css" scoped>
.card-container {
  position: relative;
  margin: 0;
  overflow: hidden;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(51, 65, 85, 0.9);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding-inline: 10px;
  opacity: 0; /* 0 */
  transition: opacity 0.5s; /*  */
}

.overlay.fadeIn {
  opacity: 1; /* 1 */
}

/*  */
.overlay::-webkit-scrollbar {
  display: none; /* WebKit */
}

.overlay {
  -ms-overflow-style: none;  /* IEEdge */
  scrollbar-width: none;  /* Firefox */
}

</style>
